$blue: #1EACC7;
$red: #FF5656;

* {
  font-family: 'Roboto', sans-serif; }
header {
  position: relative;
  height: 50px; }
nav {
  height: 52px;
  background: #212121;
  width: 100%;
  height: 50px;
  z-index: 2; }
.menu {
  position: fixed;
  display: flex;
  justify-content: space-between;
  padding: 13px 73px;
  margin: 0;
  list-style-type: none;
  .menu_link {
    color: #FFFFFF;
    font-size: 15px;
    font-weight: 300; }
  .menu_item {
    border-right: 2px dotted #000;
    padding-right: 31px;
    &:last-child {
      border-right: none; } } }
.subheader {
  position: absolute;
  top: 50px;
  width: 100%;
  background: rgba(0, 0, 0, 0.6);
  height: 87px;
  .subheader_logo {
    display: block;
    max-width: 100%;
    margin-top: 16px; }
  .subheader_official {
    margin-top: 6px;
    color: #FFFFFF;
    opacity: 0.5;
    font-weight: 100;
    font-size: 13px; }
  .subheader_call {
    color: #FFFFFF;
    margin-top: 19px;
    font-weight: 300;
    font-size: 12px;
    text-align: right; }
  .subheader_phone {
    display: block;
    margin-top: 8px;
    color: #FFFFFF;
    font-size: 22px;
    font-weight: 400;
    text-transform: uppercase;
    text-align: right; }
  .subheader_btn {
    margin-top: 19px;
    width: 172px;
    height: 46px;
    box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.12);
    background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
    text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
    color: #FFFFFF;
    font-weight: 400;
    font-size: 14px;
    border-radius: 4px;
    text-transform: uppercase; } }
.promo {
  min-height: 800px;
  background: url(../img/bg/uber_bg.jpg) center center/cover no-repeat;
  padding: 150px 0 135px 0;
  .promo_header {
    color: #FFFFFF;
    font-weight: 700;
    font-size: 60px;
    text-transform: uppercase;
    line-height: 60px;
    align-items: center;
    margin: 0;
    text-align: center; }
  .promo_subheader {
    color: #FFFFFF;
    font-weight: 700;
    font-size: 36px;
    text-transform: uppercase;
    line-height: 60px;
    text-align: center;
    margin: 0;
    margin-top: 22px; }
  .promo_descr {
    color: #FFFFFF;
    font-size: 18px;
    font-weight: 400;
    line-height: 24px;
    text-align: center;
    margin-top: 23px; }
  .promo_btn {
    display: block;
    margin: 118px auto 0 auto;
    width: 259px;
    height: 63px;
    box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.12);
    border-radius: 4px;
    background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
    text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
    color: #FFFFFF;
    font-size: 18px;
    font-weight: 400;
    text-transform: uppercase; } }
.reasons {
  padding: 71px 0 65px 0;
  .reasons_block {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 35px;
    .reasons_round {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-right: 26px;
      width: 113px;
      height: 113px;
      background-color: $blue;
      border-radius: 100%; }
    .reasons_descr {
      width: 343px;
      .reasons_subtitle {
        line-height: 21px;
        color: $blue;
        font-size: 17px;
        font-weight: 400; }
      .reasons_text {
        color: #202020;
        font-size: 15px;
        font-weight: 300;
        line-height: 21px; } } } }
.label {
  margin: 0 auto;
  height: 24px;
  width: 112px;
  padding: 5px 12px;
  background-color: #000000;
  color: #ffffff;
  font-size: 11px;
  font-weight: 300;
  text-transform: uppercase;
  text-align: center;
  &_white {
    background-color: #fff;
    color: #070716; } }
.title {
  margin-bottom: 0;
  margin-top: 38px;
  color: #222222;
  font-size: 38px;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  &_white {
    color: #ffffff; } }
.subtitle {
  margin-top: 19px;
  font-weight: 400;
  font-size: 17px;
  color: #222222;
  text-align: center;
  &_white {
    color: #FFFFFF; } }
.mobile {
  min-height: 864px;
  padding: 48px 0 79px 0;
  background: url('../img/bg/mobile_bg.jpg') center center/cover no-repeat;
  .row {
    margin-top: 32px; }
  &_item {
    position: relative;
    width: 320px;
    height: 280px;
    margin: 0 auto;
    margin-bottom: 19px;
    padding-top: 92px;
    &_1 {
      background: url('../img/mobile/mobile_screen_1.jpg') center center/cover no-repeat; }
    &_2 {
      background: url('../img/mobile/mobile_screen_2.jpg') center center/cover no-repeat; }
    &_3 {
      background: url('../img/mobile/mobile_screen_3.jpg') center center/cover no-repeat; }
    &_4 {
      background: url('../img/mobile/mobile_screen_4.jpg') center center/cover no-repeat; }
    &_5 {
      background: url('../img/mobile/mobile_screen_5.jpg') center center/cover no-repeat; }
    &_6 {
      background: url('../img/mobile/mobile_screen_6.jpg') center center/cover no-repeat; }
    &_subtitle {
      font-weight: 400;
      font-size: 24px;
      line-height: 31px;
      text-transform: uppercase;
      color: #FFFFFF;
      text-align: center; }
    &_plus {
      display: flex;
      justify-content: center;
      align-items: center;
      position: absolute;
      width: 29px;
      height: 29px;
      background: #000000;
      margin: 0 auto;
      bottom: 34px;
      left: 50%;
      transform: translateX(-50%);
      border-radius: 100%;
      padding: 6px;
      cursor: pointer; } } }
.choise {
  position: relative;
  min-height: 898px;
  background: url('../img/bg/choise_bg.png') center center/cover no-repeat;
  padding: 57px 0 138px 0;
  z-index: -2;
  .black {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 502px;
    background: #070716;
    z-index: -1; }
  &_img {
    display: block;
    position: relative;
    margin: 56px auto 0 auto;
    z-index: 2; }
  &_descr {
    text-align: center;
    color: #222222;
    font-weight: 300;
    font-size: 17px;
    line-height: 24px; } }
